import { Component, OnInit } from '@angular/core';
import { SupportService } from '../../../../../auth/_services/support.service';
import { StaffService } from '../../../../../auth/_services/staff.service';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
    selector: 'app-support-list',
    templateUrl: "support-list.component.html",
    styles: [],
    providers: [StaffService]
})
export class SupportListComponent implements OnInit {

    form: FormGroup;
    formMessage: FormGroup;
    formStatus: FormGroup;

    constructor(private _staffService: StaffService,private _supportService: SupportService, private toastr: ToastrService, private router: Router,private formBuilder: FormBuilder) { }
    public supportData;
    public chats = [];
    public staffData;
    public staff_name;
    public searchValue = "";
    public dropDownValue = '';
    p: number = 1;
    public sp_id;
    public customer_id;
    public logged_user_id;
    ngOnInit() {


        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.support_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }

        this.form = this.formBuilder.group({
            assign_to: [null, Validators.required],
        });

        this.formMessage = this.formBuilder.group({
            message: [null, Validators.required],
        })

        this.formStatus = this.formBuilder.group({
            status: [null, Validators.required],
        })
        /*user permission*/

        // localStorage.setItem('section', 'customer');

        this.getSupport();

        this.staff();
        // console.log("hello");
    }

    getSupport(){
        this._supportService.getAll().subscribe(
            data => {
                this.supportData = data.success;
            }
        );
    }

    staff() {
        this._staffService.getAll().subscribe(
            data => {
                this.staffData = data.success;
            }
        )
    }

    /*public search() {
        this._supportService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValue }).subscribe(
            data => {
                console.log(data);
                this.supportData = data.success;
            }
        );
    }*/

    public supportId(sp_id){
        console.log(sp_id);
        this.sp_id = sp_id;
    }
    public supportids(sp_id,customer_id){
        console.log(sp_id);
        console.log(customer_id);
        this.sp_id = sp_id;
        this.customer_id = customer_id;
        this.getChat(this.sp_id);
    }

    public assignTo() {
        
        let assignToid = parseInt(this.form.value.assign_to);
        let sp_id = this.sp_id;
        let data ={
            "assignToid":assignToid,
            "sp_id":sp_id
        }
        
            this._supportService.assignTo(data).subscribe(
                data => {
                    this.staff_name = data.success.f_name+' '+data.success.l_name;
                    this.getSupport();
                    this.toastr.success('Staff assign Successfully', 'Success Message');
                }
            );
    }

    public onChangePriority(e) {
        console.log(e.target.value);
        this._supportService.priorityUpdate(this.sp_id,e.target.value).subscribe(
                data => {
                    console.log(data);
                    this.staff_name = data.success.f_name+' '+data.success.l_name;
                    this.getSupport();
                    this.toastr.success('Priority updated Successfully', 'Success Message');
                }
            );
    }

    public onStatus() {
        /*console.log(this.formStatus.value.status);
        console.log(parseInt(this.formStatus.value.status));*/
        let status = parseInt(this.formStatus.value.status);
        this._supportService.updateStatus(this.sp_id,status).subscribe(
                data => {
                    console.log(data);
                    this.getSupport();
                    this.toastr.success('Status updated Successfully', 'Success Message');
                }
            );
    }

    public sendMessage(){
        console.log(this.sp_id);
        console.log(this.formMessage.value.message);
        //let data = this.formMessage.value.message;
        const data = {
          message: this.formMessage.value.message,
          customer_id: this.customer_id
        };
        this._supportService.sendMessage(this.sp_id,data).subscribe(
                data => {
                    this.getChat(this.sp_id);
                    this.reset();
                    //console.log(data);
                    /*this.staff_name = data.success.f_name+' '+data.success.l_name;
                    this.getSupport();*/
                    this.toastr.success('Message sent Successfully', 'Success Message');
                }
            );
    }

    reset() {
        this.formMessage.reset();
    }

    /*public changeStatus(customer: any) {

        if (customer.customer_status == 'Active') {
            customer.customer_status = 'Inactive';

            this._supportService.updateStatus(customer.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        } else {
            customer.customer_status = 'Active';
            this._supportService.updateStatus(customer.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        }
    }*/

    public getChat(sp_id) {
        
        this._supportService.getChat(sp_id).subscribe(
                data => {
                    //console.log(data.success);
                    this.chats = data.success;
                    var dd = JSON.parse(localStorage.getItem('currentUser'));
                    this.logged_user_id = dd.success.id;
                    //this.toastr.success('Priority updated Successfully', 'Success Message');
                }
            );
    }


}
